.contacts-group {
  &--in-intro {
    display: inline-flex;
  }

  &--if-interested {
    @media (min-width: $desktop-width) {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
    }
  }
}

.contacts-group__contact {
  display: flex;
  align-items: center;
  font-weight: 700;
  line-height: 18px;
  color: $dcg-4;

  @media (min-width: $tablet-width) {
    font-size: 28px;
    line-height: 28px;
  }

  &:hover {
    color: $mcg-6;

    & .contacts-group__svg-path {
      &--initial {
        fill: $mcg-6;
      }
    }
  }

  &:active {
    & .contacts-group__span {
      opacity: 0.3;
    }

    & .contacts-group__svg-path {
      opacity: 0.3;
    }
  }

  &--in-intro {
    width: min-content;
  }
}

.contacts-group__telephone {
  // &--inline {}

  // &--initial {}

  &--if-interested {
    @media (min-width: $desktop-width) {
      margin-right: 110px;
    }
  }
}

.contacts-group__email {
  text-transform: uppercase;

  // &--inline {}

  // &--initial {}

  // &--in-menu {}

  // &--if-interested {}
}

.contacts-group__svg {
  &--inline {
    @media (min-width: $desktop-width) {
      width: 50px;
      height: 50px;
    }
  }

  &--inline-telephone {
    &:hover {
      & .contacts-group__svg-circle {
        fill: $mcg-10;
      }
    }
  }

  &--inline-mail {
    &:hover {
      & .contacts-group__svg-circle {
        fill: $mcg-2;
      }
    }
  }

  &--initial {
    margin-right: 17px;

    @media (min-width: $tablet-width) {
      width: 68px;
      height: 68px;
      margin-right: 23px;
    }

    @media (min-width: $desktop-width) {
      width: 90px;
      height: 90px;
      margin-right: 31px;
    }
  }

  // &--initial-telephone {}

  // &--initial-mail {}

  // &:active {
  //   & .contacts-group__svg-path {
  //     opacity: 0.3;
  //   }
  // }

  &--js {
    // top: 457px;
    top: 384px;
  }

  &--scrolled {
    @media (min-width: $desktop-width) {
      top: 5px;
    }
  }
}

